import './contact.css';
import Footer from '../layouts/Footer';
import SmallHeader from '../layouts/SmallHeader';
import { Link, useNavigate } from 'react-router-dom';
import { useRef, useState } from 'react';

export default function Contacts() {
  const navigate = useNavigate();
  const formRef = useRef(null);
  const [showSuccess, setShowSuccess] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault(); //    
    
    //     
    const requiredFields = formRef.current.querySelectorAll('[required]');
    let hasEmptyFields = false;
    
    //     
    requiredFields.forEach(field => {
      field.classList.remove('error-field');
    });
    
    //    
    requiredFields.forEach(field => {
      if (!field.value.trim()) {
        field.classList.add('error-field');
        hasEmptyFields = true;
      }
    });
    
    //    ,     
    if (hasEmptyFields) {
      alert('   ');
      return;
    }
    
    //    ,    
    setShowSuccess(true);
    
    //  3       
    setTimeout(() => {
      setShowSuccess(false);
      navigate('/');
    }, 3000);
  };

  return (
    <>
      <SmallHeader />
      <div className="forma">
        <div className="form-at" ref={formRef}>
          <div className="validate-input-at w-50" data-validate=" ">
            <input
              className="input-at"
              type="text"
              name="name-at"
              placeholder=" "
              required
            />
            <span className="focus-input-at" />
          </div>
          <div className="validate-input-at w-50" data-validate=" ">
            <input
              className="input-at"
              type="text"
              name="email-at"
              placeholder="   email"
              required
            />
            <span className="focus-input-at" />
          </div>
          <div className="validate-input-at" data-validate=" ">
            <textarea
              className="input-at"
              name="message-at"
              placeholder=" "
              defaultValue={''}
              required
            />
            <span className="focus-input-at" />
          </div>
          <input
            defaultChecked={true}
            className="input-at"
            id="checkbox-at"
            type="checkbox"
            name="checkbox-at"
            required
            onChange={(e) => {
              document.getElementById('submit-at').disabled = !e.target.checked;
            }}
          />
          <label htmlFor="checkbox-at">
             ,      {' '}
            <Link to="/agreement"> </Link>
          </label>
          <input type="hidden" name="subject-at" defaultValue=" " />
          <button
            id="submit-at"
            className="form-at-btn"
            onClick={handleSubmit}
            disabled={false}
          >
            
          </button>
        </div>
        <div className="result-at" />
        
        {/*     */}
        {showSuccess && (
          <div className="success-notification">
              !
          </div>
        )}
      </div>
      <Footer />
    </>
  );
}